.sidebar{
    position: fixed;
    right: 0;
    top: 0;
    width: 23%;
    background-color: var(--color-white);
    z-index: 1000;
    padding: 24px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    transform: translateX(100%);
    transition: var(--transition);
     overflow-y: auto;
}
.sidebar-visible{
    transform: translateX(0);
}

.navbar-hide-btn{
    position: absolute;
    right: 14px;
    color: var(--color-orange);
    transition: var(--transition);
}
.navbar-hide-btn:hover{
   color: var(--color-dark);
}

.side-content .side-nav{
    margin-top: 4rem;
}
.side-item{
    padding: 6px 0;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.06);
}
.side-content{
    overflow-y: auto;
}
.side-link{
    opacity: 0.9;
    transition: var(--transition);
    font-size: 13px;
    letter-spacing: 1px;
}
.navbar-hide-link{
    font-family: 'Rajdhani', sans-serif;
    background-color: rgb(233, 48, 23);
    color: white;
   
    padding: 2px 4px;
    font-weight: bolder;
    border-radius: 10px;
    box-shadow: inset 0  0 10px rgba(0, 0, 0, 0.06);
    transition: var(--transition);
}
.navbar-hide-link:hover{
    background-color: white;
    color:  rgb(210, 51, 30) ;
    box-shadow: inset 0  7px 10px rgba(230, 34, 34, 0.699);
}

@media screen and (min-width: 76px){
    .sidebar{
        width: 280px;
       
    }
    
}