.sc-meal{
    display: grid;
}

.meal-item{
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 12, 0.09) 0px 2px 10px 0px;
    flex-direction: column;
    background: var(--color-bg);
    border-radius: 20px;


}
.meal-item img{
    height: 100px;
    width: 100px;
    position: relative;
    border-radius: 4px 4px 0 0;
    overflow: hidden;
    border: 1px solid rgba(0, 0, 0, 0.1);   
}

.meal-item-cat{
    position: absolute;
    right: 12px;
    top: 12px;
    padding: 0 18px;
    border-radius: 16px;
    font-size: 13px;
    background-color:var(--color-white);
    box-shadow: rgba(29, 24, 24, 0.2) 0px 2px 8px 0px;
}
.meal-item-body{
    border-radius: 0 0 4px 4px;
}
.meal-item-body-info{
    padding: 6px 6px 6px 12px;
}
.area{
    opacity: 0.6;
    color: #3A9691;
    font-size: 11px;
}
.meal{
    color:#cc0707;
    font-family: 'Space Mono', monospace;
    font-size: 12px;
    overflow: hidden;
    font-weight: 900;

}
@media screen and (min-width: 420px){
.sc-meal{
    grid-template-columns: repeat(2, 1fr);
    gap: 2.4rem;
}
}
@media screen and (min-width: 576px){
    .sc-meal{  
    grid-template-columns: repeat(3, 1fr);
    }
}
@media screen and (min-width: 768px){
    .sc-meal{
    grid-template-columns: repeat(4, 1fr);
}
}
@media screen and (min-width: 992px){
    .sc-meal{
    grid-template-columns: repeat(5, 1fr);
}
}



/* MealSingle */

.details-head{

}
.details-intro{
    margin-top: 16px;
    padding-right: 2rem;
    padding-left: 2rem;
}
.breadcrumb{
    background: var(--color-bg);
    border-radius: 4px;
    box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset, rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
}
.breadcrumb-item{
    font-family: 'Space Mono', monospace;
    display: flex;
    color: var(--color-white);
    letter-spacing: 5px;

   
}
.details-img{
    min-height: 420px;
    overflow: hidden;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}
.img-cover{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .details-intro .title{
    color: #F55555;
    font-family: 'Titan One', sans-serif;
    padding: 12px 0;
    font-size: 24px;
    border-bottom: 1px solid var(--color-bg)
  }
  .highlight{
    font-family: 'Rajdhani', sans-serif;
    font-weight: 800;
    color: var(--color-orange);
    letter-spacing: 2px;
    padding: 4px;
  }
  .category span:nth-child(2){
    font-family: 'Space Mono', monospace;
    color: #FCCF31;
  }
  .source{
    color: rgb(82, 82, 192);
  }
  .tags{
    display : flex;
    align-items: start;
    flex-wrap: wrap;
    
  }
  .tags h6{
    margin-right: 12px;
  }
  .tags ul{
    flex-wrap: wrap;
  }
  .tags ul li{
    margin-right: 6px;
    padding: 0 8px;
    border-radius: 3px;
    border: 1px solid var(--color-bg);
    color: blue;
  }


  .ingredients{
    background-color: #FFDEE9;
    background-image: linear-gradient(0deg, #FFDEE9 0%, #B5FFFC 100%);
        margin-top: 48px;
    margin-bottom: 48px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
     padding-bottom: 16px;
  }
  .ingredients ul{
    font-size: 13px;
    border-radius: 5px;
    margin-bottom: 7px;
    color: rgba(0, 0, 0, 0.6);
    font-weight: 500;
  }
    .li-dot{
        box-shadow: rgba(0, 0, 0, 0.09) 0px 1px 1px;
        background-color: #3A9691;
        color: var(--color-white);
        width: 20px;
        height: 20px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        font-size: 12px;
        margin-right: 8px;
        border: 0.5px solid white;
    }
  
  @media screen and (min-width: 576px){
  .ingredients ul{
    grid-template-columns: repeat(3, 1fr);
}}
@media screen and (min-width: 768px){
.ingredients ul{
    grid-template-columns: repeat(4, 1fr);
}}
@media screen and (min-width: 992px){
.ingredients ul{
    grid-template-columns: repeat(3, 1fr);
}}


.details-img{
    min-height: 420px;
    overflow: hidden;
    height: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

@media screen and (min-width: 992px){
.details-head{
    grid-template-columns: repeat(2, 1fr);
    column-gap: 40px;
}}

@media screen and (min-width: 1400px){
.details-head{
    column-gap: 60px;
}}


.measure{
    margin-top: 24px;
     margin-bottom: 24px; 
     background-color: #FFDEE9;
     background-image: linear-gradient(0deg, #FFDEE9 0%, #B5FFFC 100%);
          
}
.measure ul{
    grid-template-columns: repeat(2, 1fr);
    margin-top: 1rem;
    background-color: rgba(0, 0, 0, 0.01);
    padding: 2rem;
    
}
.measure ul li{
    margin-bottom: 8px;
    display: flex;
    align-items: end;
    color: var(--color-bg);
}
.li-icon{
    margin-right: 8px;
    color: #F55555;
}
@media screen and (min-width: 992px){

    .measures{
        margin-top: 60px;
    }
}

.instructions{
    margin-top: 24px;
     margin-bottom: 24px;
}
.instructions ul{
    margin-top: 1rem;
    font-family: 'Rajdhani', sans-serif;
}
.instructions ul li{
   margin-bottom: 1.4rem;
    display: grid;
    grid-template-columns: 28px auto;
    align-items: flex-start;

}

.li-icon{
    margin-top: 3px;
}