.navbar{
    display: flex;
    align-items: center;
    background: var(--color-bg);
   
}
#MainLogo{
    width: 40px;
    height: 40px;
    border-radius: 40%;
    box-shadow: 0 0 5px rgba(226, 10, 10, 0.5);

}
.container{
    width: 100%;
    
}
.navbar-content{
    color: var(--color-white);
}
.brand-and-toggler{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar-brand{
    display: flex;
    margin-left: 8px;
    align-items: center;
    font-size: 22px;
    color: var(--color-white);
}
.navbar-brand-text{
    font-family: 'Titan One', sans-serif;
    color: #ee2e2e;
    margin-left: 10px;
    animation: move 4s ease-in-out infinite;
    text-transform: capitalize;
    
}
.navbar-btns{
    font-family: 'Space Mono', monospace;
     display: flex;
     align-items: center;
     margin: 5px;
     color: #ee2e2e;
}

.header .header-content{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    height: 400px;
    background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.7)), url("../../assets/header.jpg") center/cover no-repeat;

}
.header-title{
    font-family: 'Titan One', sans-serif;

    margin-top: 2.4rem;
    color: var(--color-white);
    letter-spacing: 2px;
}
.header-content p{
    color: var(--color-white);
    text-transform: uppercase;
    letter-spacing: 5px;
    margin-top: 16px;
 margin-bottom: 16px;
 color: #ee2e2e;
 font-family: 'Space Mono', monospace;
opacity: 0.7;
font-weight: 900;

}
form{
    display: flex;
    align-items: center;
}
.search-form .form-control-input{
    font-family: 'Space Mono', monospace;

    box-shadow: 0 4px 10px rgb(0 0 0 / 12%), 0 0 1px rgb(0 0 0 / 5%) inset;
    min-width: 300px;
    font-size: 16px;
    height: 55px;
    padding: 0px 3.2rem;
    border-radius: 2rem;
    margin-right: 1.2rem;
}
.form-submit-btn{
    width: 50px;
    height: 50px;
    background-color: var(--color-bg);
    border: 2rem;
    border: 2px solid var(--color-white);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
    border-radius: 20px;
    transition: var(--transition);
}
.navbar-show-btn,.btn-icon{
    color: var(--color-white);
}
.form-submit-btn:hover > .btn-icon{
    color: var(--color-orange);
    transition: var(--transition);
}


@keyframes move {
    0% {
      transform: translate(-5%, 0);
    }
    50% {
      text-shadow: 0 25px 50px rgba(0, 0, 0, 0.75);
    }
    100% {
      transform: translate(30%, 0);
    }
  }
  