.adminlogin h1{
    text-align: center;
    color: #f05151;
    font-family: 'Rajdhani', sans-serif;
}
  .adminlogin img{
    width: 25px;
    padding-left: 10px;
}
/* Base styles for the admin login form */
.adminlogin {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
  }
  
  .adminbox {
    
    font-family: 'Space Mono', monospace;
    background-color: #85FFBD;
    background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%);
    
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    width: 100%;
  }
  
  .inputBox {
    padding: 20px;
}
 .adminlogin form{
  display: flex;
  flex-direction: column;
}
  
 .adminlogin input[type="text"],
  input[type="password"] {
    width: 100%;
    padding: 7px;
    margin: 10px 0;
    border: 1px solid #32def5;
    border-radius: 5px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .adminlogin input[type="submit"] {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
   letter-spacing: 4px;
    text-align: center;
    width: 50%;
    margin-bottom: 5px;
  }
  
  /* Hide/show password button styles */
  .hideShow {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  
  /* Styles for small screens (e.g., mobile phones) */
  @media screen and (max-width: 768px) {
    .adminbox {
      max-width: 100%;
      margin: 10px;
    }
  }
  .adminlogin .backHome{
    color: grey;
    text-align: center;
  }
  .adminlogin .backHome .link{
    color: #ffa600;
    font-weight: 600;
 
  }

 
  