@import url('https://fonts.googleapis.com/css2?family=Agbalumo&family=Rajdhani:wght@500&family=Space+Mono:ital@1&family=Titan+One&display=swap');
:root{
    --color-bg : linear-gradient( 135deg, #FCCF31 10%, #F55555 100%);
    --color-white : #fff;
    --color-orange : #E16120;
    --color-dark :#232323;


    --transition : all 300ms ease-in-out;
}
*,
*::after,
*::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
button{
    cursor: pointer;
    outline: 0;
    border: none;
    background-color: transparent;
    font-family: inherit;
    font-size: 1.8rem;
  }
  .container{

    margin: 0 auto;
    padding: 0 2rem;
  }
  .section-wrapper{
    padding: 80px 0;
    width: 87vw;
    /* border: 2px solid red;  */
    margin: 0 auto;  
  }
  .sc-title{
    font-family: 'Rajdhani', sans-serif;
   
    color: #FCCF31 ;
    text-transform: uppercase;
    margin-bottom: 4.8rem;
    font-weight: bold;
    letter-spacing: 14px;
    position: relative;
    font-size: 24px;
    padding: 4px 0;
    position: relative;
  }

  ul{
    list-style-type: none;
  }

  a{
    text-decoration: none;
    color: unset;
  }

  .breadcrumb{
    box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
    padding: 1rem 2rem;
    margin-bottom: 3.2rem;
  }

  .flex-center{
    display: flex;
    align-items: center;
  }
  .grid{
    display: grid;
  }