
.category-list {
  font-family: 'Rajdhani', sans-serif;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background: #f9f9f9;
  }
  
  h1 {
    text-align: center;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  table th {
    background: #007bff;
    color: #fff;
    padding: 10px;
  }
  
  table th, table td {
    padding: 10px;
    border: 1px solid #ccc;
    text-align: center;
  }
  
  /* Add styles for small screens (e.g., mobile phones) */
  @media screen and (max-width: 768px) {
    table th, table td {
      padding: 5px;
      font-weight: 600;
    }
  }
  
  /* Center the image and capitalize category name */
  table td img {
    display: block;
    margin: 0 auto;
  }
  
  table td:first-child {
    text-align: center;
    text-transform: capitalize;
  }
  
  /* Style the delete button */
  .delete {
    background-color: rgba(202, 54, 24, 0.973);
    color: #fff;
    border-radius: 10px;
    font-size: 14px;
    padding: 4px;
    cursor: pointer;
    transition: var(--transition);
  }
  .delete:hover{
  color: rgba(202, 54, 24, 0.973);
   background-color: #fff;
    border-radius: 10px;
  
  }
  .addCatLink{
    text-align: right;
    background-color: #FBAB7E;
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    color: red;
    padding: 4px;
    font-weight: bold;
    
    }
 .addCatLink #addCatLink{
  
transition: var(--transition);
 }
 .addCatLink #addCatLink:hover{
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  padding: 2px;
  background: red ;
  color: white;

 }
  
  