
.categoy_add{
  font-family: 'Rajdhani', sans-serif;
  display: flex;
  align-items: center;  
  justify-content: center;
}
  
 .CatADD h1 {
    text-align: center;
    font-family: 'Rajdhani', sans-serif;
  }
  
  .categoy_add {
    background: #63824f;
    padding: 20px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(114, 23, 23, 0.1);
  }

  .categoy_add form{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
  }
  
 .categoy_add input[type="file"],
  .categoy_add input[type="text"],
 .categoy_add textarea {
    
    width: 100%;
    height: 50%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .categoy_add .btn {
    text-align: center;
    margin-left: 15px;

  }
  
 .categoy_add input[type="submit"] {
    background-color: #eaff00a9;
    color: #020202;
    border: none;
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
    font-weight: bold;
    transition: var(--transition);
  }
 .categoy_add input[type="submit"]:hover{
    background-color: #fff;
    color: #70c90a;
    
  }
  .xyz{
    padding: 40px;
    padding-left: 20px;
  }
  
  
  
  @media screen and (max-width: 618px) {
    input[type="file"],
    input[type="text"],
    textarea {
      padding: 5px;
    }

    .categoy_add form{
       
      flex-direction: column;
  }
  .xyz{
    padding: 0;
    padding-left: 0;
  }
  .categoy_add .btn{
    margin-left: 0;

   }
  }
  
  /* Additional CSS for larger screens can be added here */
  